import type { Meta, StoryObj } from "@storybook/react";
import { Breadcrumb } from "./Breadcrumb";

const meta = {
  title: "Components/Breadcrumb",
  component: Breadcrumb,
  parameters: {
    layout: "top-left",
  },
  tags: ["autodocs"],
  argTypes: {
    data: {
      control: "object", // Enables editing in the Storybook Controls panel
      description: "The breadcrumb data (array of label-path objects).",
      defaultValue: [
        { label: "Home", path: "/" },
        { label: "About", path: "/about" },
        { label: "Services", path: "/services" },
        { label: "Contact", path: "/contact" },
      ],
    },
    isCurrentPage: {
      control: { type: "boolean" }, // Control to toggle the `isCurrentPage` property
      description: "Indicates if the last item is the current page.",
    },
  },
} satisfies Meta<typeof Breadcrumb>;

export default meta;

type Story = StoryObj<typeof meta>;

export const Default: Story = {
  args: {
    data: [
      { label: "Home", path: "/" },
      { label: "About", path: "/about" },
      { label: "Services", path: "/services" },
      { label: "Contact", path: "/contact" }, // Example current page
    ],

    isCurrentPage: false
  },
};

export const SingleLevel: Story = {
  args: {
    data: [{ label: "Home", path: "/" }], // Single current page
  },
};

export const MultiLevel: Story = {
  args: {
    data: [
      { label: "Home", path: "/" },
      { label: "Products", path: "/products" },
      { label: "Electronics", path: "/products/electronics" },
      {
        label: "Smartphones",
        path: "/products/electronics/smartphones", // Example current page
      },
    ],
  },
};
